.wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    padding: 80px 0 26px 0px;
    display: block;
    width: 300px;
    min-height: 100%;
    overflow-y: scroll;
    &:hover {
        transition: opacity 2s ease;
    }

    &:not(:hover)::-webkit-scrollbar-thumb {
        background: transparent;
    }
}

.login-wrapper {
    border-top: 1px solid rgba(22, 24, 35, 0.12);
    margin-bottom: 360px;
}

.login-content {
    padding: 16px 0 16px 0;
    color: rgba(22, 24, 35, 0.5);
    font-size: 16px;
    line-height: 21px;
    font-weight: 500;
}
