.wrapper {
    padding: 20px;
    z-index: 1;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.avatar {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    object-fit: cover;
}
.info {
    flex: 1;
    line-height: 1;
    margin: 16px 0;
}
.username {
    font-size: 1.8rem;
}
.check {
    margin-left: 4px;
    font-size: 1.4rem;
    color: rgb(32, 213, 236);
}
.name {
    font-size: 1.4rem;
    font-weight: 300;
}
.statistic {
    font-size: 1.8rem;
}
.value {
    margin-right: 4px;
}
.label {
    color: rgba(22, 24, 35, 0.5);
    margin-right: 12px;
}
