.wrapper {
    padding: 20px 0;
    width: 100%;
    max-height: 800px;
    display: flex;
    justify-content: center;
    //align-items: center;
}

.avatar {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    object-fit: cover;
}

.preview {
    left: -20px;
    top: -100px;
}

.container {
    margin-left: 12px;
}

.info {
    position: relative;
    padding-right: 120px;
    width: 510px;
    margin-bottom: 12px;
}

.name {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.check {
    margin-left: 4px;
    font-size: 1.4rem;
    color: rgb(32, 213, 236);
}

.username {
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    margin-right: 4px;
    &:hover {
        text-decoration: underline;
    }
}
.full-name {
    font-size: 1.4rem;
    font-weight: 14px;
}

.follow-btn {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 8px;
}

.desc {
    font-size: 16px;
    font-weight: 600;
    line-height: 21px;
    word-break: break-word;
}

.title {
}

.hashtag {
    a {
        margin-left: 4px;
        &:hover {
            text-decoration: underline;
        }
    }
}

.music {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
    &:hover {
        text-decoration: underline;
    }
}

.music-desc {
    margin-left: 8px;
}

.video-wrapper {
    position: relative;
    display: flex;
    align-items: flex-end;
    &::after {
        position: absolute;
        content: '';
        background-color: rgba(22, 24, 35, 0.2);
        bottom: -24px;
        left: 0;
        width: 100%;
        height: 1px;
    }
}
.video-container {
    margin-right: 20px;
    position: relative;
    video {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
    }
    &:hover {
        .play-btn,
        .volume-btn,
        .report,
        .volume-range {
            opacity: 1;
        }
    }
}

.play-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 16px;
    bottom: 20px;
    padding: 10px;
    font-size: 20px;
    width: 40px;
    height: 40px;
    transition: opacity 0.3s ease 0s;
    color: var(--white);
    opacity: 0;
    &:hover {
        cursor: pointer;
    }
}

.volume-container {
    &.active {
        opacity: 1;

        .volume-range {
            opacity: 0;
        }
    }
}

.volume-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    //font-size: 40px;
    right: 12px;
    bottom: 20px;
    //padding: 10px;
    width: 40px;
    height: 40px;
    transition: opacity 0.3s ease 0s;
    color: var(--white);
    opacity: 0;
    &:hover {
        cursor: pointer;
    }
}

.volume-range {
    display: flex;
    width: 24px;
    height: 64px;
    margin: 8px 7px;
    position: absolute;
    right: 14px;
    bottom: 50px;
    cursor: initial;
    background-color: rgba(22, 24, 35, 0.34);
    border-radius: 32px;
    opacity: 0;
    input {
        position: absolute;
        bottom: 32px;
        right: -11px;
        -webkit-appearance: none;
        background-color: #fff;
        border-radius: 4px;
        height: 2px;
        -webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
        width: 46px;

        &::-webkit-slider-thumb {
            -webkit-appearance: none;
            width: 12px;
            height: 12px;
            background-color: #fff;
            border-radius: 50%;
            cursor: pointer;
        }
    }
}

.report {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    right: 24px;
    top: 12px;
    padding: 10px;
    width: 40px;
    height: 40px;
    transition: opacity 0.3s ease 0s;
    color: var(--white);
    opacity: 0;
    span {
        margin-left: 8px;
    }
    &:hover {
        cursor: pointer;
    }
}

.video-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        font-size: 2rem;

        .yellow {
            color: #face15;
        }

        &:hover {
            cursor: pointer;
        }
    }
    strong {
        color: rgba(22, 24, 35, 0.75);
        font-size: 1.4rem;
    }
}
